<template>
  <div>
    <v-Nav :options='options' :options1='options1' :options4='options4' @creatC='deleteChange'/>
    <v-search @reeest='getManagerData' @accept='getCronData' :options='options' :options1='options1'
              :options3='options3'/>
    <v-Manager v-loading="loading" :tableData='tableData' :options='options' :options1='options1' :options3='options3'
               :options4='options4' @deleteC='deleteChange'/>
    <div class="pagin">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="10"
        background
        layout="total, prev, pager,  next ,jumper"
        :total="totalPage">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import Nav from "../common/navigator";
  import Manager from "../common/manager";
  import Search from "../common/search";
  import getAndPost from "../common/utils";

  export default {
    data() {
      return {
        currentPage: 1,
        totalPage: 1,
        tableData: [],
        loading: true,
        options: [],
        options1: [],
        options3: [],
        options4: [],
        form: {
          ip: "",
          group: "",
          state: "",
          createman: ""
        }
      };
    },
    components: {
      "v-Nav": Nav,
      "v-Manager": Manager,
      "v-search": Search
    },
    mounted() {
      this.loading = true;
      setTimeout(() => {
        this.getManagerData();
      }, 300);
      this.getIp(), this.getGroup(), this.getUsers(), this.getRepo();
    },
    methods: {
      // 分页请求数据
      handleCurrentChange(val) {
        this.loading = true;
        this.currentPage = val;
        this.getManagerData();
      },
      // 刷新页面状态
      deleteChange() {
        this.getManagerData();
      },
      // 获取子组件传过来的对象进行数据请求
      getCronData(val) {
        this.loading = true;
        this.form.ip = val.worker_id;
        this.form.group = val.group_id;
        this.form.state = val.status;
        this.form.createman = val.owner;
        this.tableData = val.data_list;
        this.totalPage = val.total_count;
        setTimeout(() => {
          this.getManagerData()
        }, 300);
      },
      // 获取cron任务
      getManagerData() {
        this.loading = true
        const url = "cap.api.views.cron_task.cron_all";
        const obj = {
          page: this.currentPage,
          num: 10,
          worker_id: this.form.ip,
          group_id: this.form.group,
          status: this.form.state,
          owner: this.form.createman
        };
        getAndPost.post(url, obj).then(res => {
          (this.tableData = res.data.data_list),
            (this.totalPage = res.data.total_count),
            (this.loading = false);
        });
      },
      // 选择器获取ip数据
      getIp() {
        const url = "cap.api.views.worker.worker_all";
        getAndPost.get(url).then(res => {
          res.data.map(item => {
            this.options.push({lable: item.ip, value: item.id});
          });
        });
      },
      // 选择器获取分组数据
      getGroup() {
        const url = "cap.api.views.group.group_all";
        getAndPost.get(url).then(res => {
          res.data.map(item => {
            this.options1.push({lable: item.name, value: item.id});
          });
        });
      },
      // 选择器获取创建人数据
      getUsers() {
        const url = "cap.api.views.user.user_list";
        getAndPost.get(url).then(res => {
          res.data.user_list.map(item => {
            this.options3.push(item.username);
          });
        });
      },
      // 选择器获取代码库数据
      getRepo() {
        const url = "cap.api.views.repo.repo_all";
        getAndPost.get(url).then(res => {
          res.data.repo_list.map(item => {
            this.options4.push({lable: item.repo_url, value: item.id});
          });
        });
      }
    }
  };
</script>

<style scoped>
  .pagin {
    text-align: center;
    padding: 30px;
  }
</style>



