<template>
  <div>
    <v-nav/>
    <div class="main">
      <div class="inpu">
        <span class="users">类型&nbsp;&nbsp;&nbsp;</span>
        <el-radio-group v-model="repo.radio" @change="radioEvent">
          <el-radio :label="2">git</el-radio>
          <el-radio :label="1">svn</el-radio>
        </el-radio-group>
        <div style="margin-bottom:20px"></div>
        <span class="users">仓库地址&nbsp;&nbsp;&nbsp;</span>
        <el-input type="text" autosize v-model="repo.url">
        </el-input>
        <div style="margin-bottom:20px"></div>
        <span class="users">仓库用户名</span>
        <el-input type="text" autosize placeholder="请输入用户名" v-model="repo.name">
        </el-input>
        <div style="margin-bottom:20px"></div>
        <span class="users">仓库密码&nbsp;&nbsp;&nbsp;</span>
        <el-input type="password" autosize placeholder="请输入密码" v-model="repo.password">
        </el-input>
        <div style="margin-bottom:20px"></div>
        <el-button type="primary" @click="addrepo">
          <span class="login">确定</span>
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import getAndPost from "../common/utils";
import Nav from "../users/usernavigator";

export default {
  data() {
    return {
      repo: {
        url: "https://",
        name: "",
        password: "",
        radio: 2
      }
    };
  },
  components: {
    "v-nav": Nav
  },
  mounted() {
    this.getUserinfo();
  },
  methods: {
    // 切换svn https 快捷输入
    radioEvent(val) {
      this.repo.url = val === 2 ? "https://" : "svn://";
    },
    // 添加代码库
    addrepo() {
      const url = "cap.api.views.repo.repo_add";
      const obj = {
        type: this.repo.radio,
        repo_url: this.repo.url,
        user: this.repo.name,
        password: this.repo.password
      };
      getAndPost.post(url, obj).then(res => {
        if (/^[0-9]+.?[0-9]*$/.test(res.data)) {
          this.$message({
            message: "添加成功",
            type: "success"
          });
          setTimeout(() => {
            this.$router.push("/cap/repolist");
          }, 1000);
        } else {
          this.$message.error(res.message);
        }
      });
    },
    getUserinfo() {
      const url = "cap.api.views.my.get_my_info";
      getAndPost.get(url).then(res => res.data);
    }
  }
};
</script>

<style scoped>
.main {
  width: 100%;
  overflow: hidden;
  text-align: center;
}

.inpu {
  width: 500px;
  margin: 0 auto;
  border-radius: 20px;
  background-color: #fff;
}

.main .login {
  margin: 20;
}

.el-input {
  width: 320px;
  margin-left: 15px;
}

.users {
  font-size: 14px;
  font-weight: 700;
}

.title {
  line-height: 60px;
  font-size: 20px;
}

.el-checkbox-group,
.el-radio-group {
  width: 340px;
  display: inline-block;
}

.el-button {
  margin-bottom: 20px;
}
</style>



