import axios from "axios";

const GetAndPost = {
  // 封装get请求
  get(url) {
    const data = axios({
      params: {
        method: url,
        session: localStorage.getItem("session")
      }
    }).then(res => res.data);
    return data;
  },
  // 封装post请求
  post(url, obj) {
    const data = axios({
      method: "post",
      params: {
        method: url,
        session: localStorage.getItem("session")
      },
      data: obj
    }).then(res => res.data);
    return data;
  }
};
export default GetAndPost;



