<template>
  <div>
    <v-nav/>
    <div class="main">
      <div class="inpu">
        <span class="users">分组名&nbsp;&nbsp;&nbsp;</span>
        <el-input type="text" autosize placeholder="请输入分组名" v-model="group.name">
        </el-input>
        <div style="margin-bottom:20px"></div>
        <el-button type="primary" @click="addGroup">
          <span class="login">确定</span>
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import getAndPost from "../common/utils";
import Nav from "../users/usernavigator";

export default {
  data() {
    return {
      group: {
        name: ""
      }
    };
  },
  components: {
    "v-nav": Nav
  },
  mounted() {
    this.getUserinfo();
  },
  methods: {
    addGroup() {
      const obj = {
        name: this.group.name
      };
      const url = "cap.api.views.group.group_add";
      getAndPost.post(url, obj).then(res => {
        if (res.data === true) {
          this.$message({
            message: "添加成功",
            type: "success"
          });
          this.$router.push("grouplist");
        } else {
          this.$message.error(res.formError.message);
        }
      });
    },
    getUserinfo() {
      const url = "cap.api.views.my.get_my_info";
      getAndPost.get(url).then(res => res.data);
    }
  }
};
</script>

<style scoped>
.main {
  width: 100%;
  overflow: hidden;
  text-align: center;
}

.inpu {
  width: 500px;
  margin: 0 auto;
  border-radius: 20px;
  background-color: #fff;
}

.main .login {
  margin: 20;
}

.el-input {
  width: 320px;
  margin-left: 15px;
}

.users {
  font-size: 14px;
  font-weight: 700;
}

.title {
  line-height: 60px;
  font-size: 20px;
}

.el-checkbox-group,
.el-radio-group {
  width: 340px;
  display: inline-block;
}

.el-button {
  margin-bottom: 20px;
}
</style>



