<template>
  <div>
    <!-- 列表页 -->
    <el-table :data="tableData" border stripe style="width: 100%" class="managerstyle">
      <el-table-column fixed prop="tid" label="ID" width="60" :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column prop="name" label="任务名" width="80" :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column prop="status" label="状态" width="90" :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column :prop="rule" :label="tab" width="120" :show-overflow-tooltip="true">
        <template slot-scope="scope">
          <div v-show="$route.path==='/cap/task'&&scope.row.is_running===false" class="stopped">stopped({{scope.row.run_times}})</div>
          <div v-show="$route.path==='/cap/task'&&scope.row.is_running===true" class="running">running({{scope.row.run_times}})</div>
          <div v-show="$route.path==='/cap/cron'">{{scope.row.rule}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="group.name" label="所属组" width="90" :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column prop="info" label="功能" width="120" :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column prop="owner" label="创建人" width="70" :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column label="服务器IP" :show-overflow-tooltip="true" width="150">
        <template slot-scope="scope">
          <span>{{scope.row.worker.ip}}</span>
          <!-- 判断服务器球颜色 -->
          <div type="text" size="small" :class="scope.row.worker.is_alive===true? 'green':'brown'"></div>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="389" id="caozuo">
        <template slot-scope="scope">
          <el-button @click="onOrOff(scope.row)" type="text" size="small">{{scope.row.status==='启用'? '禁用':'启用'}}
          </el-button>
          <el-button @click="openDialogForm(scope.row,scope.$index)" type="text" size="small">修改</el-button>
          <el-button @click="handleClick(scope.row)" type="text" size="small">部署日志</el-button>
          <el-button @click="codeClick(scope.row)" type="text" size="small">运行记录</el-button>
          <el-button @click="handleStrike(scope.row)" type="text" size="small">{{restart}}</el-button>
          <el-button @click="handleRemove(scope.row)" type="text" size="small">删除</el-button>
          <el-button @click="clone(scope.row)" type="text" size="small">克隆</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 部署日志dialog -->
    <el-dialog title="部署日志" :visible.sync="dialogVisible" width="50%" top="6vh">
      <div class="codeR">
        <div class="codeState">执行状态:{{dialogTabDate.state}}</div>
        <div class="codeAddtime">添加时间:{{dialogTabDate.addtime}}</div>
        <div class="codeFinishtime">结束时间:{{dialogTabDate.finishtime}}</div>
        <div class="codeStdout"><pre>输出日志:{{dialogTabDate.stdout}}</pre></div>
        <div class="codeStderr"><pre>错误日志:{{dialogTabDate.stderr}}</pre></div>
      </div>
    </el-dialog>
    <!-- 运行记录dialog -->
    <el-dialog title="运行记录" :visible.sync="dialogVisibleRun" width="70%" top="6vh" class="rundia">
      <el-table :data="runData" border style="width: 100%">
        <el-table-column fixed prop="version" :show-overflow-tooltip="true" label="版本" width="90">
        </el-table-column>
        <el-table-column prop="begintime" label="开始时间" :show-overflow-tooltip="true" width="170">
        </el-table-column>
        <el-table-column prop="endtime" label="结束时间" :show-overflow-tooltip="true" width="170">
        </el-table-column>
        <el-table-column prop="status" label="状态" :show-overflow-tooltip="true" width="140">
        </el-table-column>
        <el-table-column prop="stdout" label="输出日志" :show-overflow-tooltip="true" width="188">
        </el-table-column>
        <el-table-column prop="stderror" label="错误日志" :show-overflow-tooltip="true" width="197">
        </el-table-column>
        <el-table-column label="操作" width="180">
          <template slot-scope="scope">
            <el-button @click="handleDetails(scope.$index)" type="text" size="small">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @current-change='changeRun' background layout="prev, pager, next" :page-size="10"
                     :page-count="totalPages" class="runPagin">
      </el-pagination>
    </el-dialog>
    <!-- 修改dialog -->
    <el-dialog :visible.sync="dialogFormVisible" top="10vh" title="修改计划任务" width="40%">
      <el-form :model="form">
        <el-form-item label="Worker节点" :label-width="formLabelWidth">
          <el-select v-model="form.ip" clearable placeholder="请选择">
            <el-option v-for="(item,index) in options" :key="index" :label="item.lable" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="分组" :label-width="formLabelWidth">
          <el-select v-model="form.group" clearable placeholder="组">
            <el-option v-for="(item,index) in options1" :key="index" :label="item.lable" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="名称" :label-width="formLabelWidth">
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item :label="tab" :label-width="formLabelWidth" v-show="this.$route.path==='/cap/cron'">
          <el-input v-model="form.rule" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="代码库ID" :label-width="formLabelWidth">
          <el-select v-model="form.codeId" clearable placeholder="组">
            <el-option v-for="(item,index) in options4" :key="index" :label="item.lable" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="代码库版本" :label-width="formLabelWidth">
          <el-input v-model="form.versionC" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="预构建命令" :label-width="formLabelWidth">
          <el-input v-model="form.beforeOrder" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="运行命令" :label-width="formLabelWidth">
          <el-input v-model="form.nowOrder" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="说明" :label-width="formLabelWidth">
          <el-input v-model="form.info" auto-complete="off" type="textarea"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible =false">取 消</el-button>
        <el-button type="primary" @click="submitChange">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 运行记录详情dialog -->
    <el-dialog title="任务详情" :visible.sync="runDialogVisibleDitals" width="60%" top="6vh">
      <div class="codeR">
        <div class="codeState">执行状态:{{runDitals.status}}</div>
        <div class="codeAddtime">版本:{{runDitals.version}}</div>
        <div class="codeAddtime">开始时间:{{runDitals.begintime}}</div>
        <div class="codeFinishtime">结束时间:{{runDitals.endtime}}</div>
        <div class="codeStdout"><pre>输出日志:{{runDitals.stdout}}</pre></div>
        <div class="codeStderr"><pre>错误日志:{{runDitals.stderror}}</pre></div>
      </div>
    </el-dialog>
    <!-- 克隆任务模态框 -->
        <el-dialog :visible.sync="cloneDialogFormVisible" top="15vh" :title="dialogTitle" width="40%" custom-class='dialogstyle'>
      <el-form :model="cloneform">
        <el-form-item label="Worker节点" :label-width="formLabelWidth">
          <el-select v-model="cloneform.ip" clearable placeholder="请选择">
            <el-option v-for="(item,index) in options" :key="index" :label="item.lable" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="分组" :label-width="formLabelWidth">
          <el-select v-model="cloneform.group" clearable placeholder="组">
            <el-option v-for="(item,index) in options1" :key="index" :label="item.lable" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="名称" :label-width="formLabelWidth">
          <el-input v-model="cloneform.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="时间规则" :label-width="formLabelWidth" v-if='isShow'>
          <el-input v-model="cloneform.rule" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="代码库" :label-width="formLabelWidth">
          <el-select v-model="cloneform.codeId" clearable placeholder="代码库">
            <el-option v-for="(item,index) in options2" :key="index" :label="item.lable" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="代码库版本" :label-width="formLabelWidth">
          <el-input v-model="cloneform.versionC" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="预构建命令" :label-width="formLabelWidth">
          <el-input v-model="cloneform.beforeOrder" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="运行命令" :label-width="formLabelWidth">
          <el-input v-model="cloneform.nowOrder" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="说明" :label-width="formLabelWidth">
          <el-input v-model="cloneform.info" auto-complete="off" type="textarea"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cloneDialogFormVisible =false">取 消</el-button>
        <el-button type="primary" @click='cloneSubmit'>确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import getAndPost from "../common/utils";

export default {
  props: ["tableData", "options", "options1", "options4"],
  data() {
    return {
      tab: this.$route.meta[2],
      dialogVisible: false,
      cloneDialogFormVisible: false,
      restart: this.$route.meta[3],
      dialogFormVisible: false,
      dialogVisibleRun: false,
      form: {
        ip: "",
        group: "",
        name: "",
        rule: "",
        codeId: "",
        versionC: "",
        beforeOrder: "",
        nowOrder: "",
        info: ""
      },
      cloneform: {
        name: "",
        rule: "* * * * *",
        codeId: "",
        versionC: "",
        beforeOrder: "",
        info: "",
        nowOrder: "",
        workerId: "",
        groupId: ""
      },
      formLabelWidth: "120px",
      dialogTabDate: [],
      changeRow: "",
      resetRow: "",
      runData: [],
      curpage: 1,
      runId: "",
      totalPages: 1,
      runDialogVisibleDitals: false,
      runDitals: [],
      options2: [],
      dialogTitle: this.$route.meta[1]
    };
  },
  mounted() {},
  computed: {
    // 不同组件显示不同内容
    rule() {
      if (this.$route.path === "/cap/cron") {
        return "rule";
      } else {
        return "run_times";
      }
    },
    isShow() {
      if (this.$route.path === "/cap/cron") {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    // 部署日志
    handleClick(row) {
      if (this.$route.path === "/cap/cron") {
        this.dialogVisible = true;
        const obj = {
          tid: row.tid
        };
        const url = "cap.api.views.cron_task.cron_publog";
        getAndPost.post(url, obj).then(res => {
          res.data[0].addtime = new Date(parseInt(res.data[0].addtime) * 1000)
            .toLocaleString()
            .substr(0, 19);
          res.data[0].finishtime = new Date(
            parseInt(res.data[0].finishtime) * 1000
          )
            .toLocaleString()
            .substr(0, 19);
          this.dialogTabDate = res.data[0];
        });
      } else {
        this.dialogVisible = true;
        const obj = {
          tid: row.tid
        };
        const url = "cap.api.views.deamon_task.deamon_publog";
        getAndPost.post(url, obj).then(res => {
          res.data[0].addtime = new Date(parseInt(res.data[0].addtime) * 1000)
            .toLocaleString()
            .substr(0, 19);
          res.data[0].finishtime = new Date(
            parseInt(res.data[0].finishtime) * 1000
          )
            .toLocaleString()
            .substr(0, 19);
          this.dialogTabDate = res.data[0];
        });
      }
    },
    // 运行记录
    codeClick(row) {
      this.runId = row.tid;
      this.dialogVisibleRun = true;
      if (this.$route.path === "/cap/cron") {
        this.runCronDialog();
      } else {
        this.runTaskDialog();
      }
    },
    // cron请求运行记录信息
    runCronDialog() {
      const url = "cap.api.views.cron_task.cron_runlog";
      const obj = {
        tid: this.runId,
        page: this.curpage
      };
      getAndPost.post(url, obj).then(res => {
        this.totalPages = res.data.total_page || 1;
        res.data.data_list.map(item => {
          item.begintime = new Date(parseInt(item.begintime) * 1000)
            .toLocaleString()
            .substr(0, 19);
          item.endtime = new Date(parseInt(item.endtime) * 1000)
            .toLocaleString()
            .substr(0, 19);
        });
        this.runData = res.data.data_list;
      });
    },
    // task运行记录
    runTaskDialog() {
      const url = "cap.api.views.deamon_task.deamon_runlog";
      const obj = {
        tid: this.runId,
        page: this.curpage
      };
      getAndPost.post(url, obj).then(res => {
        this.totalPages = res.data.total_page;
        res.data.data_list.map(item => {
          item.begintime = new Date(parseInt(item.begintime) * 1000)
            .toLocaleString()
            .substr(0, 19);
          item.endtime = new Date(parseInt(item.endtime) * 1000)
            .toLocaleString()
            .substr(0, 19);
        });
        this.runData = res.data.data_list;
      });
    },

    // 修改信息
    openDialogForm(row, index) {
      this.dialogFormVisible = true;
      this.changeRow = row.tid;
      if (this.$route.path === "/cap/cron") {
        this.getCronFile();
      } else {
        this.getTaskFile();
      }
      this.resetRow = index;
    },
    // 提交修改
    submitChange() {
      if (this.$route.path === "/cap/cron") {
        this.submitCron();
      } else {
        this.submitTask();
      }
    },
    // 修改cron
    submitCron() {
      const url = "cap.api.views.cron_task.cron_edit";
      const obj = {
        tid: this.changeRow,
        name: this.form.name,
        repo_id: this.form.codeId,
        version: this.form.versionC,
        pre_build: this.form.beforeOrder,
        info: this.form.info,
        run_cmd: this.form.nowOrder,
        worker_id: this.form.ip,
        group_id: this.form.group,
        rule: this.form.rule
      };
      getAndPost.post(url, obj).then(res => {
        if (res.data === true) {
          this.$message({
            type: "success",
            message: "修改成功"
          });
          this.dialogFormVisible = false;
          const obj = {
            tid: this.changeRow
          };
          const url = "cap.api.views.cron_task.cron_one_info";
          getAndPost.post(url, obj).then(res => {
            this.tableData.splice(this.resetRow, 1, res.data);
          });
        } else {
          this.$message.error(res.formError.message);
        }
      });
    },
    // 修改task
    submitTask() {
      const url = "cap.api.views.deamon_task.deamon_edit";
      const obj = {
        tid: this.changeRow,
        name: this.form.name,
        rule: this.form.rule,
        repo_id: this.form.codeId,
        version: this.form.versionC,
        pre_build: this.form.beforeOrder,
        info: this.form.info,
        run_cmd: this.form.nowOrder,
        worker_id: this.form.ip,
        group_id: this.form.group
      };
      getAndPost.post(url, obj).then(res => {
        if (res.data === true) {
          this.$message({
            type: "success",
            message: "修改成功"
          });
          this.dialogFormVisible = false;
          const obj = {
            tid: this.changeRow
          };
          const url = "cap.api.views.deamon_task.deamon_one_info";
          getAndPost.post(url, obj).then(res => {
            this.tableData.splice(this.resetRow, 1, res.data);
          });
        } else {
          this.$message.error(res.formError.message);
        }
      });
    },
    // 删除
    handleRemove(row) {
      this.$confirm("此操作将永久删除该内容, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          if (this.$route.path === "/cap/cron") {
            const obj = {
              tid: row.tid
            };
            const url = "cap.api.views.cron_task.cron_delete";
            getAndPost.post(url, obj).then(res => {
              if (res.data === true) {
                this.$emit("deleteC");
                setTimeout(() => {
                  this.$message({
                    message: "删除成功",
                    type: "success"
                  });
                }, 100);
              } else {
                this.$message.error(res.formError.message);
              }
            });
          } else {
            const obj = {
              tid: row.tid
            };
            const url = "cap.api.views.deamon_task.deamon_delete";
            getAndPost.post(url, obj).then(res => {
              if (res.data === true) {
                this.$emit("deleteT");
                setTimeout(() => {
                  this.$message({
                    message: "删除成功",
                    type: "success"
                  });
                }, 100);
              } else {
                this.$message.error(res.formError.message);
              }
            });
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 启用or禁用
    onOrOff(row) {
      // 禁用
      if (row.status === "启用") {
        if (this.$route.path === "/cap/cron") {
          const url = "cap.api.views.cron_task.cron_disable";
          const obj = {
            tid: row.tid
          };
          getAndPost.post(url, obj).then(res => {
            if (res.data === true) {
              this.$message({
                message: "正在禁用",
                type: "success"
              });
              this.$emit("deleteC");
            } else {
              this.$emit("deleteC");
              this.$message.error(res.message);
            }
          });
        } else {
          const url = "cap.api.views.deamon_task.deamon_disable";
          const obj = {
            tid: row.tid
          };
          getAndPost.post(url, obj).then(res => {
            if (res.data === true) {
              this.$message({
                message: "正在禁用",
                type: "success"
              });
              this.$emit("deleteT");
            } else {
              this.$emit("deleteT");
              this.$message.error(res.message);
            }
          });
        }
      } else {
        // 启用
        if (this.$route.path === "/cap/cron") {
          const url = "cap.api.views.cron_task.cron_enable";
          const obj = {
            tid: row.tid
          };
          getAndPost.post(url, obj).then(res => {
            if (res.data === true) {
              this.$message({
                message: "正在启用",
                type: "success"
              });
              this.$emit("deleteC");
            } else {
              this.$message.error(res.message);
              setTimeout(() => {
                this.$emit("deleteC");
              }, 300);
            }
          });
        } else {
          const url = "cap.api.views.deamon_task.deamon_enable";
          const obj = {
            tid: row.tid
          };
          getAndPost.post(url, obj).then(res => {
            if (res.data === true) {
              this.$message({
                message: "正在启用",
                type: "success"
              });
              this.$emit("deleteT");
            } else {
              this.$message.error(res.message);
              setTimeout(() => {
                this.$emit("deleteT");
              }, 300);
            }
          });
        }
      }
    },
    // 获取单个cron数据
    getCronFile() {
      const obj = {
        tid: this.changeRow
      };
      const url = "cap.api.views.cron_task.cron_one_info";
      getAndPost.post(url, obj).then(res => {
        this.form.ip = res.data.worker.id;
        this.form.group = res.data.group.id;
        this.form.name = res.data.name;
        this.form.rule = res.data.rule;
        this.form.info = res.data.info;
        this.form.versionC = res.data.version;
        this.form.codeId = res.data.repo_id;
        this.form.nowOrder = res.data.run_cmd;
        this.form.beforeOrder = res.data.pre_build;
      });
    },
    // 获取单个task数据
    getTaskFile() {
      const obj = {
        tid: this.changeRow
      };
      const url = "cap.api.views.deamon_task.deamon_one_info";
      getAndPost.post(url, obj).then(res => {
        this.form.ip = res.data.worker.id;
        this.form.group = res.data.group.id;
        this.form.name = res.data.name;
        this.form.rule = res.data.rule;
        this.form.info = res.data.info;
        this.form.versionC = res.data.version;
        this.form.codeId = res.data.repo_id;
        this.form.nowOrder = res.data.run_cmd;
        this.form.beforeOrder = res.data.pre_build;
      });
    },
    // 获取单个cron数据
    cloneCronFile() {
      const obj = {
        tid: this.changeRow
      };
      const url = "cap.api.views.cron_task.cron_one_info";
      getAndPost.post(url, obj).then(res => {
        this.cloneform.ip = res.data.worker.id;
        this.cloneform.group = res.data.group.id;
        this.cloneform.name = res.data.name;
        this.cloneform.rule = res.data.rule;
        this.cloneform.info = res.data.info;
        this.cloneform.versionC = res.data.version;
        this.cloneform.codeId = res.data.repo_id;
        this.cloneform.nowOrder = res.data.run_cmd;
        this.cloneform.beforeOrder = res.data.pre_build;
      });
    },
    // 获取单个task数据
    cloneTaskFile() {
      const obj = {
        tid: this.changeRow
      };
      const url = "cap.api.views.deamon_task.deamon_one_info";
      getAndPost.post(url, obj).then(res => {
        this.cloneform.ip = res.data.worker.id;
        this.cloneform.group = res.data.group.id;
        this.cloneform.name = res.data.name;
        this.cloneform.rule = res.data.rule;
        this.cloneform.info = res.data.info;
        this.cloneform.versionC = res.data.version;
        this.cloneform.codeId = res.data.repo_id;
        this.cloneform.nowOrder = res.data.run_cmd;
        this.cloneform.beforeOrder = res.data.pre_build;
      });
    },
    // 运行记录分页请求
    changeRun(val) {
      this.curpage = val;
      if (this.$route.path === "/cap/cron") {
        this.runCronDialog();
      } else {
        this.runTaskDialog();
      }
    },
    // 运行记录查看详情
    handleDetails(val) {
      this.runDialogVisibleDitals = true;
      const obj = {
        tid: this.runId,
        page: this.curpage,
        index: val,
        path: this.$route.path
      };
      if (this.$route.path === "/cap/cron") {
        this.runCronDialogDitals(obj.tid, obj.page, obj.index);
      } else {
        this.runTaskDialogDitals(obj.tid, obj.page, obj.index);
      }
    },
    // 立刻出发
    handleStrike(val) {
      if (this.$route.path === "/cap/cron") {
        const url = "cap.api.views.cron_task.cron_run_once";
        const obj = {
          tid: val.tid
        };
        getAndPost.post(url, obj).then(res => {
          if (res.data === true) {
            this.$message({
              type: "success",
              message: "正在触发"
            });
          } else {
            this.$message.error(res.message);
          }
        });
      } else {
        const url = "cap.api.views.deamon_task.deamon_restart_now";
        const obj = {
          tid: val.tid
        };
        getAndPost.post(url, obj).then(res => {
          if (res.data === true) {
            this.$message({
              type: "success",
              message: "正在重启"
            });
          } else {
            this.$message.error(res.message);
          }
        });
      }
    },
    // 获取计划任务运行记录
    runCronDialogDitals(id, pg, thispage) {
      const url = "cap.api.views.cron_task.cron_runlog";
      const obj = {
        tid: id,
        page: pg
      };
      getAndPost.post(url, obj).then(res => {
        console.log(res);
        this.runDitals = res.data.data_list[thispage];
        this.runDitals.begintime = new Date(
          parseInt(this.runDitals.begintime) * 1000
        )
          .toLocaleString()
          .substr(0, 19);
        this.runDitals.endtime = new Date(
          parseInt(this.runDitals.endtime) * 1000
        )
          .toLocaleString()
          .substr(0, 19);
      });
    },
    // 获取后台运行记录
    runTaskDialogDitals(id, pg, thispage) {
      const url = "cap.api.views.deamon_task.deamon_runlog";
      const obj = {
        tid: id,
        page: pg
      };
      getAndPost.post(url, obj).then(res => {
        this.runDitals = res.data.data_list[thispage];
        this.runDitals.begintime = new Date(
          parseInt(this.runDitals.begintime) * 1000
        )
          .toLocaleString()
          .substr(0, 19);
        this.runDitals.endtime = new Date(
          parseInt(this.runDitals.endtime) * 1000
        )
          .toLocaleString()
          .substr(0, 19);
      });
    },
    // 克隆任务
    clone(row) {
      this.changeRow = row.tid;
      this.getRepo();
      if(this.$route.path==='/cap/cron'){
        this.cloneCronFile()
      }else{
        this.cloneTaskFile()
      }
      this.cloneDialogFormVisible = true;  
    },
    // 下拉列表获取代码库数据
    getRepo() {
      const url = "cap.api.views.repo.repo_all";
      getAndPost.get(url).then(res => {
        this.options2 = [];
        res.data.repo_list.map(item => {
          this.options2.push({ lable: item.repo_url, value: item.id });
        });
      });
    },
    cloneSubmit() {
      // 克隆任务
      if(this.$route.path==='/cap/cron'){
        this.cloneCron()
      }else{
        this.cloneTask()
      }
    },
    // 克隆计划任务
    cloneCron() {
      const url = "cap.api.views.cron_task.cron_add";
      const obj = {
        name: this.cloneform.name,
        rule: this.cloneform.rule,
        repo_id: this.cloneform.codeId,
        version: this.cloneform.versionC,
        pre_build: this.cloneform.beforeOrder,
        info: this.cloneform.info,
        run_cmd: this.cloneform.nowOrder,
        worker_id: this.cloneform.ip,
        group_id: this.cloneform.group
      };
      getAndPost.post(url, obj).then(res => {
        if (res.data === true) {
          this.$message({
            message: "创建计划任务成功",
            type: "success"
          });
          this.cloneDialogFormVisible = false;
          this.$emit("deleteC");
        } else {
          this.$message.error(res.message);
        }
      });
    },
    // 克隆后台任务
    cloneTask() {
      const url = "cap.api.views.deamon_task.deamon_add";
      const obj = {
        name: this.cloneform.name,
        repo_id: this.cloneform.codeId,
        version: this.cloneform.versionC,
        pre_build: this.cloneform.beforeOrder,
        info: this.cloneform.info,
        run_cmd: this.cloneform.nowOrder,
        worker_id: this.cloneform.ip,
        group_id: this.cloneform.group
      };
      getAndPost.post(url, obj).then(res => {
        if (res.data === true) {
          this.$message({
            message: "创建后台任务成功",
            type: "success"
          });
          this.cloneDialogFormVisible = false;
          this.$emit("deleteT");
        } else {
          this.$message.error(res.message);
        }
      });
    }
  }
};
</script>
<style scoped>
.nav .left {
  border-left: 2px solid #000;
  padding: 5px;
  font-weight: 700;
}

.nav .right {
  position: absolute;
  right: 20px;
  top: 30px;
}

.el-input {
  width: 200px;
}

.el-form-item {
  margin-bottom: 5px;
}

.el-table {
  min-height: 639px;
}

.codeR div {
  width: 97%;
  /* height: 50px; */
  line-height: 50px;
  margin-top: 15px;
  border-radius: 5px;
  color: #000;
  font-weight: 700;
  padding-left: 22px;
  font-size: 15px;
}

.green {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: #67c23a;
  display: inline-block;
  margin-left: 10px;
  position: relative;
  top: 3px;
}

.brown {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: #909399;
  display: inline-block;
  margin-left: 10px;
  position: relative;
  top: 3px;
}

.codeR .codeStdout {
  max-height: 350px;
  overflow-x: scroll;
  overflow-y: scroll;
}
.codeR .codeStderr {
  max-height: 350px;
  overflow-x: scroll;
  overflow-y: scroll;
}

.el-select {
  width: 90%;
}

.el-input {
  width: 90%;
}

.codeState {
  background-color: #67c23a;
}

.codeAddtime {
  background-color: #909399;
}

.codeFinishtime {
  background-color: #909399;
}

.codeStdout {
  background-color: #409eff;
}

.codeStderr {
  background: #f56c6c;
}

.runPagin {
  margin-top: 15px;
  margin-bottom: 5px;
  text-align: center;
}

.el-button {
  font-size: 14px;
}
.running {
  background-color: #67c23a;
  color: #fff;
}
.stopped {
  background-color: #e4e7ed;
}
</style>




