<template>
  <div>
    <v-nav/>
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column fixed prop="id" label="分组ID" width="280">
      </el-table-column>
      <el-table-column prop="name" label="分组名" width="280">
      </el-table-column>
      <el-table-column prop="addtime" label="分组创建时间" width="380">
      </el-table-column>
      <el-table-column label="操作" width="229">
        <template slot-scope="scope">
          <el-button @click="handleChange(scope.row)" type="text" size="medium">修改</el-button>
          <el-button @click="handleRemove(scope.row)" type="text" size="medium">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      :title="`修改${groupname}`"
      :visible.sync="dialogVisible"
      width="40%">
      <div class="main">
        <div class="inpu">
          <span class="users">分组ID&nbsp;&nbsp;</span>
          <el-input type="text" autosize v-model="group.id" :disabled='true'>
          </el-input>
          <div style="margin-bottom:20px"></div>
          <span class="users">分组名&nbsp;&nbsp;&nbsp;</span>
          <el-input type="text" autosize placeholder="请输入新的组名" v-model="group.name">
          </el-input>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="resetPwd">确 定</el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
  import getAndPost from "../common/utils";
  import Nav from "../users/usernavigator";

  export default {
    data() {
      return {
        dialogVisible: false,
        groupname: "",
        tableData: [],
        group: {
          name: "",
          id: ""
        }
      };
    },
    components: {
      "v-nav": Nav
    },
    mounted() {
      this.getGroupList();
    },
    methods: {
      // 传递dialog信息
      handleChange(row) {
        this.dialogVisible = true;
        this.group.name = row.name;
        this.group.id = row.id;
      },
      // 删除分组
      handleRemove(row) {
        this.$confirm("此操作将永久删除该分组, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          const url = "cap.api.views.group.group_delete";
          const obj = {
            id: row.id,
            name: this.group.name
          };
          getAndPost.post(url, obj).then(res => {
            console.log(res);
            if (res.data === true) {
              this.$message({
                type: "success",
                message: "删除成功!"
              });
              this.getGroupList();
            } else {
              this.$message.error(res.message);
            }
          });
        });
      },
      // 修改分组信息
      resetPwd() {
        const url = "cap.api.views.group.group_edit";
        const obj = {
          id: this.group.id,
          name: this.group.name
        };
        getAndPost.post(url, obj).then(res => {
          if (res.data === true) {
            this.$message({
              message: "修改成功",
              type: "success"
            });
            this.dialogVisible = false;
            this.group.id = "";
            this.group.name = "";
            this.getGroupList();
          } else {
            this.$message.error(res.formError.message);
          }
        });
      },
      // 获取分组列表
      getGroupList() {
        const url = "cap.api.views.group.group_all";
        getAndPost.get(url).then(res => {
          this.tableData = res.data;
          this.tableData.map(item => {
            item.addtime = new Date(item.addtime * 1000).toLocaleString();
          });
        });
      }
    }
  };
</script>

<style scoped>
  .el-table {
    color: black;
    font-size: 14px;
  }

  .state {
    background-color: #337ab7;
    color: #fff;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 7px;
    font-weight: 700;
    margin-right: 15px;
  }

  .users {
    font-size: 14px;
    font-weight: 700;
  }

  .el-checkbox-group,
  .el-radio-group {
    width: 340px;
    display: inline-block;
  }

  .main {
    width: 100%;
    overflow: hidden;
    text-align: center;
  }

  .inpu {
    width: 500px;
    margin: 0 auto;
    border-radius: 20px;
    background-color: #fff;
  }

  .el-input {
    width: 320px;
    margin-left: 15px;
  }

  .users {
    font-size: 14px;
    font-weight: 700;
  }

  .title {
    line-height: 60px;
    font-size: 20px;
  }

  .el-checkbox-group,
  .el-radio-group {
    width: 340px;
    display: inline-block;
  }
</style>



