<template>
  <div class="navigator">
    <div class="nav">
      <div>
          <span class="left">
              {{ $route.meta[0]}}
          </span>
      </div>
      <el-button type="primary" class="right" @click="creatFile">{{ $route.meta[1]}}</el-button>
    </div>
    <!-- 创建计划任务模态框 -->
    <el-dialog :visible.sync="dialogFormVisible" top="15vh" :title="dialogTitle" width="40%" custom-class='dialogstyle'>
      <el-form :model="form">
        <el-form-item label="Worker节点" :label-width="formLabelWidth">
          <el-select v-model="form.ip" clearable placeholder="请选择">
            <el-option v-for="(item,index) in options" :key="index" :label="item.lable" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="分组" :label-width="formLabelWidth">
          <el-select v-model="form.group" clearable placeholder="组">
            <el-option v-for="(item,index) in options1" :key="index" :label="item.lable" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="名称" :label-width="formLabelWidth">
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="时间规则" :label-width="formLabelWidth" v-if='isShow'>
          <el-input v-model="form.rule" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="代码库" :label-width="formLabelWidth">
          <el-select v-model="form.codeId" clearable placeholder="代码库">
            <el-option v-for="(item,index) in options2" :key="index" :label="item.lable" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="代码库版本" :label-width="formLabelWidth">
          <el-input v-model="form.versionC" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="预构建命令" :label-width="formLabelWidth">
          <el-input v-model="form.beforeOrder" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="运行命令" :label-width="formLabelWidth">
          <el-input v-model="form.nowOrder" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="说明" :label-width="formLabelWidth">
          <el-input v-model="form.info" auto-complete="off" type="textarea"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible =false">取 消</el-button>
        <el-button type="primary" @click='creatSubmit'>确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import getAndPost from "../common/utils";

  export default {
    data() {
      return {
        dialogFormVisible: false,
        form: {
          name: "",
          rule: "* * * * *",
          codeId: "",
          versionC: "",
          beforeOrder: "",
          info: "",
          nowOrder: "",
          workerId: "",
          groupId: ""
        },
        options: [],
        options1: [],
        options2: [],
        formLabelWidth: "120px",
        dialogTitle: this.$route.meta[1]
      };
    },
    computed: {
      isShow() {
        if (this.$route.path === "/cap/cron") {
          return true;
        } else {
          return false;
        }
      }
    },
    mounted() {
    },
    methods: {
      // 获取各个列表数据
      creatFile() {
        this.dialogFormVisible = true;
        this.getIp();
        this.getGroup();
        this.getRepo();
      },
      // 执行创建
      creatSubmit() {
        if (this.$route.path === "/cap/cron") {
          this.creatCron();
        } else {
          this.creatTask();
        }
      },
      // 创建计划任务
      creatCron() {
        const url = "cap.api.views.cron_task.cron_add";
        const obj = {
          name: this.form.name,
          rule: this.form.rule,
          repo_id: this.form.codeId,
          version: this.form.versionC,
          pre_build: this.form.beforeOrder,
          info: this.form.info,
          run_cmd: this.form.nowOrder,
          worker_id: this.form.ip,
          group_id: this.form.group
        };
        getAndPost.post(url, obj).then(res => {
          if (res.data === true) {
            this.$message({
              message: "创建计划任务成功",
              type: "success"
            });
            this.dialogFormVisible = false;
            this.$emit("creatC");
          } else {
            this.$message.error(res.message);
          }
        });
      },
      // 创建后台任务
      creatTask() {
        const url = "cap.api.views.deamon_task.deamon_add";
        const obj = {
          name: this.form.name,
          repo_id: this.form.codeId,
          version: this.form.versionC,
          pre_build: this.form.beforeOrder,
          info: this.form.info,
          run_cmd: this.form.nowOrder,
          worker_id: this.form.ip,
          group_id: this.form.group
        };
        getAndPost.post(url, obj).then(res => {
          if (res.data === true) {
            this.$message({
              message: "创建后台任务成功",
              type: "success"
            });
            this.dialogFormVisible = false;
            this.$emit("deleteT");
          } else {
            this.$message.error(res.message);
          }
        });
      },
      // 下拉列表获取ip数据
      getIp() {
        const url = "cap.api.views.worker.worker_all";
        getAndPost.get(url).then(res => {
          this.options = [];
          res.data.map(item => {
            this.options.push({lable: item.ip, value: item.id});
          });
        });
      },
      // 下拉列表获取分组数据
      getGroup() {
        const url = "cap.api.views.group.group_all";
        getAndPost.get(url).then(res => {
          this.options1 = [];
          res.data.map(item => {
            this.options1.push({lable: item.name, value: item.id});
          });
        });
      },
      // 下拉列表获取代码库数据
      getRepo() {
        const url = "cap.api.views.repo.repo_all";
        getAndPost.get(url).then(res => {
          this.options2 = [];
          res.data.repo_list.map(item => {
            this.options2.push({lable: item.repo_url, value: item.id});
          });
        });
      }
    }
  };
</script>
<style scoped>
  .navigator {
    height: 80px;
  }

  .nav {
    float: left;
    font-size: 24px;
    margin-top: 5px;
    height: 80px;
    line-height: 80px;
    position: relative;
    width: 100%;
  }

  .nav .left {
    border-left: 2px solid #409EFF;
    padding: 5px;
    font-weight: 700;
  }

  .nav .right {
    position: absolute;
    right: 20px;
    top: 30px;
  }

  .el-input {
    width: 70%;
  }

  .el-form-item {
    margin-bottom: 5px;
  }

  .el-select {
    width: 70%;
  }

  .dialogstyle {
    border-radius: 20px;
  }
</style>



