<template>
  <div class="goodlist">
    <div class="header">
      <div class="container">
        <div class="left" @click="gohome">
          <b>CAP系统</b>
        </div>
        <div class="left clearfix">
          <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" background-color="rgb(51,51,51)" text-color="#fff" active-text-color="#ffd04b" router>
            <el-menu-item index="/cap/cron" >计划任务</el-menu-item>
            <el-menu-item index="/cap/task" >后台任务</el-menu-item>
            <el-submenu index="1" >
              <template slot="title">用户管理</template>
              <el-menu-item index="/cap/useradd">添加用户</el-menu-item>
              <el-menu-item index="/cap/userlist">用户列表</el-menu-item>
            </el-submenu>
            <el-submenu index="2" >
              <template slot="title" >代码库</template>
              <el-menu-item index="/cap/repoadd">添加代码库</el-menu-item>
              <el-menu-item index="/cap/repolist">代码库列表</el-menu-item>
            </el-submenu>
            <el-submenu index="3" >
              <template slot="title">分组</template>
              <el-menu-item index="/cap/groupadd" >添加分组</el-menu-item>
              <el-menu-item index="/cap/grouplist">分组列表</el-menu-item>
            </el-submenu>
            <!-- <el-menu-item index="/cap/worker">节点负载</el-menu-item> -->
          </el-menu>
        </div>
        <div class="right">
          欢迎：
          <span>{{userinfo}}</span>
          /
          <a @click="checkpwd">修改密码</a>
          /
          <a @click="goOut">注销</a>
        </div>
      </div>
    </div>
    <div class="content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import getAndPost from "../components/common/utils";
export default {
  data() {
    return {
      activeIndex: "cron",
      userinfo: ""
    };
  },
  mounted() {
    // 刷新页面不改变导航条active
    this.checkUserinfo();
    this.activeIndex = this.$route.path;
  },
  methods: {
    // 修改登陆账户密码
    checkpwd() {
      this.$router.push("changepwd");
    },
    // 首页
    gohome() {
      this.$router.push("/cap/cron");
    },
    // 退出登陆
    goOut() {
      const url = "cap.api.views.login.logout";
      getAndPost.get(url).then(res => {
        if (res.data) {
          setTimeout(() => {
            this.$router.push("/login")
          }, 1000);
          this.$message("已退出登陆");
        }
      });
    },
    // 获取登陆用户信息
    checkUserinfo() {
      const url = "cap.api.views.my.get_my_info";
      getAndPost.get(url).then(res => {
        if(res.errorCode===0){
          this.userinfo = res.data.username;
        }else{
          this.$router.push('/login')
        }
      });
    }
  },
  components: {}
};
</script>

<style scoped>
.header {
  width: 100%;
  background-color: rgb(51, 51, 51);
  font-weight: 700;
  color: #9d9d9d
}

.container {
  width: 1170px;
  margin: 0 auto;
  color: #fff;
  height: 50px;
  overflow: hidden;
}

.container .left {
  float: left;
  line-height: 50px;
  font-size: 15px;
  font-weight: 700;
  position: relative;
}

.el-menu {
  font-size: 16px;
  color: #ccc;
}

.title {
  color: #fff;
}

.goHome {
  float: left;
  color: #fff;
}

.left {
  margin-right: 50px;
  text-align: center;
  cursor: pointer;
}

.right {
  float: right;
  color: #ccc;
  font-size: 14px;
}

.right a {
  color: #fff;
  text-decoration: none;
  line-height: 50px;
  color: #ccc;
  cursor: pointer;
  font-size: 13px;
}

.right a:hover {
  color: #fff;
}

.right span {
  color: #fff;
  font-weight: 700;
  font-size: 17px;
}

.content {
  width: 1170px;
  margin: 0 auto;
  background-color: #fff;
}

.goodlist {
  height: 100%;
}
</style>



