<template>
  <div>
    <v-nav/>
    <div class="main">
      <div class="inpu">
        <span class="users">旧密码&nbsp;&nbsp;&nbsp;</span>
        <el-input type="password" autosize placeholder="请输入旧密码" v-model="userinfo.oldpwd">
        </el-input>
        <div style="margin-bottom:20px"></div>
        <span class="users">新密码&nbsp;&nbsp;&nbsp;</span>
        <el-input type="password" autosize placeholder="请输入新密码" v-model="userinfo.newpwd">
        </el-input>
        <div style="margin-bottom:20px"></div>
        <span class="users">确认密码</span>
        <el-input type="password" autosize placeholder="请确认新密码" v-model="userinfo.newpwdYes">
        </el-input>
        <div style="margin-bottom:20px"></div>
        <el-button type="primary" @click="changePwd">
          <span class="login">确定</span>
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import Nav from "../../components/users/usernavigator";
import getAndPost from "../common/utils";

export default {
  data() {
    return {
      userinfo: {
        oldpwd: "",
        newpwd: "",
        newpwdYes: ""
      }
    };
  },
  mounted(){
    this.getUserinfo()
  },
  methods: {
    changePwd() {
      const url = "cap.api.views.my.change_my_password";
      const obj = {
        password_old: this.userinfo.oldpwd,
        password: this.userinfo.newpwd,
        password_again: this.userinfo.newpwdYes
      };
      getAndPost.post(url, obj).then(res => {
        console.log(res);
        if (res.data === true) {
          setTimeout(() => {
            this.$message({
              message: "修改成功",
              type: "success"
            });
          }, 100);
          this.$router.push("/login");
          // setTimeout(() => {
          this.$message.error("当前登陆已失效，请重新登录");
          // }, 100);
        } else {
          this.$message.error(res.formError.message);
        }
      });
    },
    getUserinfo() {
      const url = "cap.api.views.my.get_my_info";
      getAndPost.get(url).then(res => res.data)
    }
  },
  components: {
    "v-nav": Nav
  }
};
</script>

<style scoped>
.main {
  width: 100%;
  overflow: hidden;
  text-align: center;
  padding-top: 40px;
}

.inpu {
  width: 500px;
  margin: 0 auto;
  border-radius: 20px;
  background-color: #fff;
}

.main .login {
  margin: 20;
}

.el-input {
  width: 320px;
  margin-left: 15px;
}

.users {
  font-size: 14px;
  font-weight: 700;
}

.title {
  line-height: 60px;
  font-size: 20px;
}

.el-checkbox-group,
.el-radio-group {
  width: 340px;
  display: inline-block;
}

.el-button {
  margin-bottom: 20px;
}
</style>



