<template>
  <div>
    <v-nav/>
    <el-table
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        fixed
        prop="id"
        label="用户ID"
        width="180">
      </el-table-column>
      <el-table-column
        prop="username"
        label="用户名"
        width="280">
      </el-table-column>
      <el-table-column
        prop="addtime"
        label="用户创建时间"
        width="280">
      </el-table-column>
      <el-table-column
        label="用户状态"
        width="280"
        prop='is_admin'
      >
      </el-table-column>
      <el-table-column
        label="操作"
        width="149">
        <template slot-scope="scope">
          <el-button @click="handleChange(scope.row)" type="text" size="medium">修改</el-button>
          <el-button @click="handleRemove(scope.row)" type="text" size="medium">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      :title="`修改用户${username}`"
      :visible.sync="dialogVisible"
      width="40%">
      <div class="main">
        <div class="inpu">
          <span class="users">用户名&nbsp;&nbsp;&nbsp;</span>
          <el-input type="text" autosize placeholder="请输入用户名" v-model="userinfo.username">
          </el-input>
          <div style="margin-bottom:20px"></div>
          <span class="users">密&nbsp;&nbsp;&nbsp;码&nbsp;&nbsp;&nbsp;</span>
          <el-input type="password" autosize placeholder="请输入密码" v-model="userinfo.password">
          </el-input>
          <div style="margin-bottom:20px"></div>
          <span class="users">确认密码</span>
          <el-input type="password" autosize placeholder="请再次确认密码" v-model="userinfo.passwordYes">
          </el-input>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="resetPwd">确 定</el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
import Nav from "./usernavigator";
import getAndPost from "../common/utils";

export default {
  data() {
    return {
      dialogVisible: false,
      username: "",
      tableData: [],
      userinfo: {
        username: "",
        password: "",
        passwordYes: ""
      }
    };
  },
  components: {
    "v-nav": Nav
  },
  methods: {
    // 传递dialog信息
    handleChange(row) {
      this.dialogVisible = true;
      this.username = row.username;
      this.userinfo.username = row.username;
    },
    // 删除用户
    handleRemove(row) {
      this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        const url = "cap.api.views.user.user_delete";
        const obj = {
          username: row.username
        };
        getAndPost.post(url, obj).then(res => {
          if (res.data === true) {
            this.$message({
              type: "success",
              message: "删除成功!"
            });
            this.getUsersInfo();
          }
        });
      });
    },
    // 修改密码
    resetPwd() {
      const url = "cap.api.views.user.user_resetpwd";
      const obj = {
        username: this.userinfo.username,
        password: this.userinfo.password,
        password_again: this.userinfo.passwordYes
      };
      getAndPost.post(url, obj).then(res => {
        if (res.data === true) {
          this.$message({
            message: "修改成功",
            type: "success"
          });
          this.dialogVisible = false;
          this.userinfo.password = "";
          this.userinfo.passwordYes = "";
        } else {
          this.$message.error(res.formError.message);
        }
      });
    },
    // 获取用户列表
    getUsersInfo() {
      const url = "cap.api.views.user.user_list";
      getAndPost.get(url).then(res => {
        this.tableData = res.data.user_list;
        this.tableData.map(item => {
          item.is_admin =
            item.is_admin === true ? (
              <span class="super"> 超级用户 </span>
            ) : (
              <span class="ordinary"> 普通用户 </span>
            );
          item.addtime = new Date(item.addtime * 1000).toLocaleString();
        });
      });
    }
  },
  mounted() {
    this.getUsersInfo();
  }
};
</script>
<style scoped>
.el-table {
  color: black;
  font-size: 14px;
}

.state {
  background-color: #337ab7;
  color: #fff;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 7px;
  font-weight: 700;
  margin-right: 15px;
}

.users {
  font-size: 14px;
  font-weight: 700;
}

.el-checkbox-group,
.el-radio-group {
  width: 340px;
  display: inline-block;
}

.main {
  width: 100%;
  overflow: hidden;
  text-align: center;
}

.inpu {
  width: 500px;
  margin: 0 auto;
  border-radius: 20px;
  background-color: #fff;
}

.el-input {
  width: 320px;
  margin-left: 15px;
}

.users {
  font-size: 14px;
  font-weight: 700;
}

.title {
  line-height: 60px;
  font-size: 20px;
}

.el-checkbox-group,
.el-radio-group {
  width: 340px;
  display: inline-block;
}

.super {
  background-color: #409eff;
  padding: 5px 20px;
  border-radius: 1px solid #409eff;
  border-radius: 5px;
  display: inline-block;
}

.ordinary {
  padding: 5px 20px;
  border-radius: 5px;
  border: 1px solid #ccc;
  display: inline-block;
}
</style>



