import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/Login/login'
import cap from '../components/cap'
import Checkpwd from '../components/pages/checkpwd'
import Userlist from '../components/users/userlist'
import Useradd from '../components/users/useradd'
import Cron from '../components/pages/cron'
import Task from '../components/pages/task'
// import Pub from '../components/pages/pub'
import Histor from '../components/pages/history'
import Needvertifytask from '../components/pages/needvertifytask'
import Mypub from '../components/pages/mypub'
import TaskLogsdetail from "../components/pages/taskLogsdetail";
import Nofound from '../components/pages/noFound'
import Repoadd from '../components/repos/repoadd'
import Repolist from '../components/repos/repolist'
import Groupadd from '../components/group/grouptadd'
import Grouplist from '../components/group/grouplist'
import Worker from '../components/worker/worker'
import Particulars from '../components/common/particulars'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "/",
      redirect: "/login"
    },
    {
      path: "/login",
      component: Login
    },
    {
      path: "/cap",
      component: cap,
      // 路由前置守卫 判断用户是否登录
      beforeEnter: (to, from, next) => {
        if (localStorage.getItem("session")) {
          next();
        } else {
          next({ path: "/login" });
        }
      },
      children: [
        {
          path: "",
          redirect: "cron"
        },
        // {
        //   path: "pub",
        //   component: Pub,
        //   meta: ["发布/回滚", "待审核任务", "所有记录"]
        // },
        {
          path: "cron",
          component: Cron,
          meta: ["计划任务管理", "创建计划任务", "时间规则", "立刻触发"]
        },
        {
          path: "details",
          name:'details',
          component: Particulars,
          meta: ["运行记录详情"]
        },
        {
          path: "task",
          component: Task,
          meta: ["后台任务管理", "创建后台任务", "运行状态", "立即重启"]
        },
        {
          path: "/task/logsdetail/:rid",
          component: TaskLogsdetail
        },
        {
          path: "useradd",
          component: Useradd,
          meta: ["添加用户"]
        },
        {
          path: "userlist",
          component: Userlist,
          meta: ["用户列表"]
        },
        {
          path: "changepwd",
          component: Checkpwd,
          meta: ["修改密码"]
        },
        {
          path: "history",
          component: Histor,
          meta: ["所有发布任务", "我的发布记录"]
        },
        {
          path: "needvertifytask",
          component: Needvertifytask,
          meta: ["待审核任务"]
        },
        {
          path: "mypub",
          component: Mypub,
          meta: ["我的发布任务"]
        },
        {
          path: "repoadd",
          component: Repoadd,
          meta: ["添加代码库"]
        },
        {
          path: "repolist",
          component: Repolist,
          meta: ["代码库列表"]
        },
        {
          path: "groupadd",
          component: Groupadd,
          meta: ["添加分组"]
        },
        {
          path: "worker",
          component: Worker,
          meta: ["节点负载"]
        },
        {
          path: "grouplist",
          component: Grouplist,
          meta: ["分组列表"]
        },
        // 404
        {
          path: "*",
          component: Nofound
        }
      ]
    }
  ]
});



