<template>
  <div>
      <v-nav/>
      <div class="charts">
          <div :id="'myChart'+index" v-for="(item,index) in ary" :key="index" :style="{width:'500px',height:'500px'}"></div>
      </div>
  </div>
</template>

<script>
import Nav from "../users/usernavigator";
import getAndPost from "../common/utils";
import echarts from "echarts";
export default {
  data() {
    return {
      id: "",
      ip: "",
      ary: [],
      canbox:''
    };
  },
  mounted() {
    this.getAllWorkers();
    this.draw();
  },
  components: {
    "v-nav": Nav
  },
  methods: {
    // 绘制
    draw() {},
    //   获取所有的worker节点信息
    getAllWorkers() {
      const url = "cap.api.views.worker.worker_all";
      getAndPost.get(url).then(res => {
        console.log(res)
        res.data.map(item => {
          this.ary.push(item.id);
        });
        this.ary.map((item,index) => {
          this.id = item;
          this.canbox=item;
          this.getOneWorker();
          this.getOneWorkerCpu();
        });
      });
    },
    // 获取单个节点cpu信息
    getOneWorkerCpu() {
      const url = "cap.api.views.worker.worker_cpu_mem_log";
      const obj = {
        id: this.id
      };
      getAndPost.post(url, obj).then(res => {
        const option = {
          title: {
            text: this.ip,
            x: "center"
          },
          tooltip: {
            trigger: "axis",
            formatter: function(params) {
              return (
                params[0].name +
                "<br/>" +
                params[0].seriesName +
                " : " +
                params[0].value +
                " (%)<br/>" +
                params[1].seriesName +
                " : " +
                params[1].value +
                " (%)"
              );
            }
          },
          legend: {
            data: ["cpu负载", "内存负载"],
            padding: 35,
            x: "center"
          },
          toolbox: {
            show: false,
            feature: {
              mark: { show: true },
              dataView: { show: true, readOnly: false },
              magicType: { show: true, type: ["line", "bar"] },
              restore: { show: true },
              saveAsImage: { show: true }
            }
          },
          dataZoom: {
            show: true,
            realtime: true,
            start: 0,
            end: 100
          },
          xAxis: [
            {
              type: "category",
              boundaryGap: false,
              axisLine: { onZero: false },
              data: []
            }
          ],
          yAxis: [
            {
              name: "cpu负载(%)",
              type: "value",
              max: 100
            },
            {
              name: "内存负载(%)",
              type: "value",
              max: 100,
              axisLabel: {
                formatter: function(v) {
                  return v;
                }
              }
            }
          ],
          series: [
            {
              name: "cpu负载",
              type: "line",
              itemStyle: { normal: { areaStyle: { type: "default" } } },
              data: []
            },
            {
              name: "内存负载",
              type: "line",
              yAxisIndex: 1,
              itemStyle: { normal: { areaStyle: { type: "default" } } },
              data: []
            }
          ]
        };
        res.data.map((item, index) => {
          item.time = new Date(parseInt(item.time) * 1000)
            .toLocaleString()
            .substr(0, 16)
            .substring(10, 16);
          option.xAxis[0].data.push(item.time);
          option.series[0].data.push(item.cpu_percent);
          option.series[1].data.push(item.mem_percent);
        });
          let mychart = echarts.init(document.getElementById("myChart"+this.canbox));
               console.log(option)
              mychart.setOption(option);
      });
    },
    // 获取单个节点的信息
    getOneWorker() {
      const url = "cap.api.views.worker.worker_info";
      const obj = {
        id: this.id
      };
      getAndPost.post(url, obj).then(res => {
        this.ip = `${res.data.ip}节点`;
      });
    }
  }
};
</script>

<style scoped>
.charts {
  min-height: 700px;
}
.charts div {
  float: left;
  margin-left: 50px;
}
</style>



