<template>
  <div>
    <!-- 运行记录详情页 -->
    <v-nav/>
    <div class="codeR">
      <div class="codeState">状态:{{form.status}}</div>
      <div class="codeFinishtime">版本:{{form.version}}</div>
      <div class="codeAddtime">开始时间:{{form.begintime}}</div>
      <div class="codeAddtime">结束时间:{{form.endtime}}</div>
      <div class="codeStdout">输出日志:{{form.stdout}}</div>
      <div class="codeStderr">
        <pre>
          错误日志:{{form.stderror}}
        </pre>
        </div>
    </div>
  </div>
</template>

<script>
import Nav from "../users/usernavigator";
import getAndPost from "../common/utils";

export default {
  data() {
    return {
      form: {}
    };
  },
  mounted() {
    if (this.$route.params.path === "/cap/cron") {
      this.runCronDialog();
    } else {
      this.runTaskDialog();
    }
  },
  components: {
    "v-nav": Nav
  },
  methods: {
    // 获取计划任务运行记录
    runCronDialog() {
      const url = "cap.api.views.cron_task.cron_runlog";
      const obj = {
        tid: this.$route.params.tid,
        page: this.$route.params.page
      };
      getAndPost.post(url, obj).then(res => {
        this.form = res.data.data_list[this.$route.params.page];
        this.form.begintime = new Date(parseInt(this.form.begintime) * 1000)
          .toLocaleString()
          .substr(0, 19);
        this.form.endtime = new Date(parseInt(this.form.endtime) * 1000)
          .toLocaleString()
          .substr(0, 19);
      });
    },
    // 获取后台运行记录
    runTaskDialog() {
      const url = "cap.api.views.deamon_task.deamon_runlog";
      const obj = {
        tid: this.$route.params.tid,
        page: this.$route.params.page
      };
      getAndPost.post(url, obj).then(res => {
        this.form = res.data.data_list[this.$route.params.page];
        this.form.begintime = new Date(parseInt(this.form.begintime) * 1000)
          .toLocaleString()
          .substr(0, 19);
        this.form.endtime = new Date(parseInt(this.form.endtime) * 1000)
          .toLocaleString()
          .substr(0, 19);
      });
    }
  }
};
</script>

<style scoped>
.codeR div {
  width: 97%;
  line-height: 50px;
  margin-top: 15px;
  border-radius: 5px;
  color: #000;
  font-weight: 700;
  padding-left: 22px;
  font-size: 15px;
}

.green {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: #67c23a;
}

.brown {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: #909399;
}

.el-select {
  width: 90%;
}

.el-input {
  width: 90%;
}

.codeState {
  background-color: #67c23a;
}

.codeAddtime {
  background-color: #909399;
}

.codeFinishtime {
  background-color: #909399;
}

.codeStdout {
  background-color: #409eff;
  max-height: 400px;
  overflow-x: scroll;
  overflow-y: scroll;
}

.codeStderr {
  background: #f56c6c;
  height: 400px;
  overflow-x: scroll;
  overflow-y: scroll;
}
</style>



