<template>
  <div>
    <div class="nav">
      <div>
          <span class="left">
              {{ $route.meta[0]}}
          </span>
      </div>
      <el-button type="primary" class="right" @click="gomypub">{{ $route.meta[1]}}</el-button>
    </div>
    应用：
    <el-select v-model="form.project" clearable placeholder="请选择">
      <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    <el-select v-model="form.project1" clearable placeholder="请选择">
      <el-option
        v-for="item in options1"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    环境：
    <el-select v-model="form.envior" clearable placeholder="请选择">
      <el-option
        v-for="item in options2"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    用户：
    <el-select v-model="form.users" clearable placeholder="请选择">
      <el-option
        v-for="item in options3"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    起始时间：
    <el-date-picker
      v-model="form.startTime"
      type="date"
      placeholder="选择日期">
    </el-date-picker>
    截止时间：
    <el-date-picker
      v-model="form.endTime"
      type="date"
      placeholder="选择日期时间">
    </el-date-picker>
    <el-button type="primary">查询</el-button>
    <div class="table">
      <el-table  :data="tableData"  border  style="width: 100%">
        <el-table-column  fixed  prop="date"  label="项目"  width="100">
        </el-table-column>
        <el-table-column  prop="name"  label="应用"  width="100">
        </el-table-column>
        <el-table-column  prop="province"  label="环境"  width="100">
        </el-table-column>
        <el-table-column  prop="city"  label="操作"  width="130">
        </el-table-column>
        <el-table-column  prop="address"  label="操作人"  width="150">
        </el-table-column>
        <el-table-column  prop="zip"  label="操作原因"  width="109">
        </el-table-column>
        <el-table-column  prop="zip"  label="状态"  width="130">
        </el-table-column>
        <el-table-column  prop="zip"  label="操作时间"  width="175">
        </el-table-column>
        <el-table-column  prop="zip"   label="审核信息"  width="175">
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>

  export default {
    data() {
      return {
        tableData: [],
        form: {
          project: "",
          project1: "",
          envior: "",
          users: "",
          startTime: "",
          endTime: ""
        }
      };
    },
    computed: {
      options() {
        return historyOptions.historyOptions.options;
      },
      options1() {
        return historyOptions.historyOptions.options1;
      },
      options2() {
        return historyOptions.historyOptions.options2;
      },
      options3() {
        return historyOptions.historyOptions.options3;
      },
      options4() {
        return historyOptions.historyOptions.options4;
      }
    },
    methods: {
      gomypub() {
        this.$router.push("mypub");
      }
    }
  };
</script>

<style scoped>
  .el-select {
    width: 104px;
  }

  .search {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 20px;
  }

  .inputFun {
    width: 150px;
  }

  .el-date-editor {
    width: 150px;
  }

  .table {
    margin-top: 30px;
  }

  .nav {
    float: left;
    font-size: 24px;
    margin-top: 5px;
    height: 80px;
    line-height: 80px;
    position: relative;
    width: 100%;
  }

  .nav .left {
    border-left: 2px solid #000;
    padding: 5px;
    font-weight: 700;
  }

  .nav .right {
    position: absolute;
    right: 20px;
    top: 30px;
  }
</style>



