<template>
            <!-- 复用组件 -->
  <div class="navigator">
    <div class="nav">
      <div>
          <span class="left">
              {{ $route.meta[0]}}
          </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  components: {}
};
</script>
<style scoped>
.navigator {
  height: 80px;
}

.nav {
  float: left;
  font-size: 24px;
  margin-top: 5px;
  height: 80px;
  line-height: 80px;
  position: relative;
  width: 100%;
}

.nav .left {
  border-left: 2px solid #409EFF;
  padding: 5px;
  font-weight: 700;
}

.nav .right {
  position: absolute;
  right: 20px;
  top: 30px;
}
</style>



