<template>
  <div class="main">
    <div class="inpu">
      <div class="title">登陆</div>
      <span class="users">用户名</span>
      <el-input type="text" autosize placeholder="请输入用户名" v-model="userinfo.username">
      </el-input>
      <div style="margin-bottom:20px"></div>
      <span class="users">密&nbsp;&nbsp;&nbsp;码</span>
      <el-input type="password" autosize placeholder="请输入密码" v-model="userinfo.password">
      </el-input>
      <div style="margin-bottom:20px"></div>
      <el-button type="primary" @click='getLogin'>
        <span class="login">登陆</span>
      </el-button>
      <el-button type="primary" @click="clearbox">
        <span class="clear">清空</span>
      </el-button>
    </div>
  </div>
</template>

<script>
import getAndPost from "../common/utils";
import axios from "axios";
// import apiDomain from "../common/config";
export default {
  data() {
    return {
      userinfo: {
        username: "",
        password: ""
      },
      data: 0
    };
  },
  mounted() {
    this.getUserinfo();
  },
  methods: {
    clearbox() {
      this.userinfo = {
        username: "",
        password: ""
      };
    },
    // 验证登录
    getLogin() {
      // 检测本地是否存有session_key 获取session
      if (localStorage.getItem("session") === null) {
        axios.create({ baseURL: "" })
          .get("/api/get_session_key").then(res => {
          localStorage.setItem("session", res.data.data);
          this.Loginvalidation();
        });
      } else {
        this.Loginvalidation();
      }
    },
    Loginvalidation() {
      const obj = {
        username: this.userinfo.username,
        password: this.userinfo.password
      };
      const url = "cap.api.views.login.login";
      getAndPost.post(url, obj).then(res => {
        if (res.data) {
          this.$message({
            message: "登陆成功",
            type: "success"
          });
          setTimeout(() => {
            this.$router.push("/cap/cron");
          }, 1000);
        } else {
          this.$message.error(res.formError.message);
        }
      });
    },
    getUserinfo() {
      const url = "cap.api.views.my.get_my_info";
      getAndPost.get(url).then(res => {
        if (res.errorCode === 0) {
          this.$router.push("/cap/cron");
        }
      });
    }
  }
};
</script>

<style scoped>
.main {
  height: 100%;
  width: 100%;
  background-color: #324057;
  overflow: hidden;
  text-align: center;
  color: #324057;
}

.inpu {
  width: 420px;
  height: 280px;
  margin: 300px auto;
  border-radius: 20px;
  background-color: #fff;
}

.main .login {
  margin: 20;
}

.el-input {
  width: 320px;
  margin-left: 15px;
}

.users {
  font-size: 14px;
  color: #3c763d;
}

.title {
  line-height: 60px;
  font-size: 20px;
}
</style>



