<template>
  <div id="search">
    主机：
    <el-select v-model="form.ip" clearable placeholder="请选择">
      <el-option
        v-for="(item,index) in options"
        :key="index"
        :label="item.lable"
        :value="item.value">
      </el-option>
    </el-select>
    组：
    <el-select v-model="form.group" clearable placeholder="组">
      <el-option
        v-for="(item,index) in options1"
        :key="index"
        :label="item.lable"
        :value="item.value">
      </el-option>
    </el-select>
    状态：
    <el-select v-model="form.state" clearable placeholder="请选择">
      <el-option
        v-for="(item,index) in options2"
        :key="index"
        :label="item.lable"
        :value="item.value">
      </el-option>
    </el-select>
    创建人：
    <el-select v-model="form.createman" clearable placeholder="请选择">
      <el-option
        v-for="(item,index) in options3"
        :key="index"
        :label="item"
        :value="item">
      </el-option>
    </el-select>
    <el-button type="primary" @click='search' class="searchCron">查询</el-button>
    <el-button type="primary" @click='reset' class="reset-task">刷新当前状态</el-button>
  </div>
</template>

<script>
  import getAndPost from "../common/utils";

  export default {
    props: ["options", "options1", "options3"],
    data() {
      return {
        form: {
          ip: "",
          group: "",
          state: "",
          createman: ""
        },
        taskTableData: [],
        cornTableData: [],
        options2: [
          {
            lable: "禁用",
            value: -1
          },
          {
            lable: "启用",
            value: 1
          },
          {
            lable: "待部署",
            value: 0
          },
          {
            lable: "正在部署",
            value: 2
          },
          {
            lable: "部署失败",
            value: 3
          }
        ]
      };
    },
    mounted() {
    },
    methods: {
      // 查询
      search() {
        if (this.$route.path === "/cap/cron") {
          this.getCornData();
        } else {
          this.getTaskData();
        }
      },
      // 刷新当前状态
      reset() {
        if (this.$route.path === "/cap/cron") {
          this.$emit("reeest");
        } else {
          this.$emit("reeest");
        }
      },
      // 查询计划任务
      getCornData() {
        const url = "cap.api.views.cron_task.cron_all";
        const obj = {
          worker_id: this.form.ip,
          group_id: this.form.group,
          status: this.form.state,
          owner: this.form.createman
        };
        getAndPost.post(url, obj).then(res => {
          this.cornTableData = res.data;
          this.cornTableData.worker_id = this.form.ip;
          this.cornTableData.group_id = this.form.group;
          this.cornTableData.status = this.form.state;
          this.cornTableData.owner = this.form.createman;
          this.$emit("accept", this.cornTableData);
        });
      },
      // 查询后台任务
      getTaskData() {
        const url = "cap.api.views.deamon_task.deamon_all";
        const obj = {
          worker_id: this.form.ip,
          group_id: this.form.group,
          status: this.form.state,
          owner: this.form.createman
        };
        getAndPost.post(url, obj).then(res => {
          this.taskTableData = res.data;
          this.taskTableData.worker_id = this.form.ip;
          this.taskTableData.group_id = this.form.group;
          this.taskTableData.status = this.form.state;
          this.taskTableData.owner = this.form.createman;
          this.$emit("accept", this.taskTableData);
        });
      }
    }
  };
</script>

<style scoped>
  .el-select {
    width: 170px;
  }

  #search {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 20px;
  }

  #search  .reset-task {
    margin-left: 70px;
  }
</style>



