<template>
  <div>
    <v-nav/>
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      v-loading="loading"
      class="managerstyle">
      <el-table-column
        fixed
        prop="type"
        label="类型"
        width="100">
      </el-table-column>
      <el-table-column
        fixed
        prop="id"
        label="代码库ID"
        width="100">
      </el-table-column>
      <el-table-column
        prop="repo_url"
        label="仓库地址"
        width="280">
      </el-table-column>
      <el-table-column
        prop="addtime"
        label="添加时间"
        width="180">
      </el-table-column>
      <el-table-column
        prop="user"
        label="仓库用户名"
        width="180">
      </el-table-column>
      <el-table-column
        prop="password"
        label="仓库密码"
        width="160">
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="120">
        <template slot-scope="scope">
          <el-button @click="handleChange(scope.row)" type="text" size="medium">修改</el-button>
          <el-button @click="deleteChange(scope.row)" type="text" size="medium">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="修改"
      :visible.sync="dialogVisible"
      width="50%">
      <div class="main">
        <div class="inpu">
          <span class="users">类型&nbsp;&nbsp;&nbsp;</span>
          <el-radio-group v-model="repo.radio" @change="radioEvent">
            <el-radio :label="2">git</el-radio>
            <el-radio :label="1">svn</el-radio>
          </el-radio-group>
          <div style="margin-bottom:20px"></div>
          <span class="users">仓库地址&nbsp;&nbsp;&nbsp;</span>
          <el-input type="text" autosize placeholder="https://或svn://开头" v-model="repo.url">
          </el-input>
          <div style="margin-bottom:20px"></div>
          <span class="users">仓库用户名</span>
          <el-input type="text" autosize placeholder="请输入用户名" v-model="repo.name">
          </el-input>
          <div style="margin-bottom:20px"></div>
          <span class="users">仓库密码&nbsp;&nbsp;&nbsp;</span>
          <el-input type="password" autosize placeholder="请输入密码" v-model="repo.password">
          </el-input>
          <div style="margin-bottom:20px"></div>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="changeRepo">确 定</el-button>
           </span>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import getAndPost from "../common/utils";
import Nav from "../users/usernavigator";

export default {
  data() {
    return {
      dialogVisible: false,
      repo: {
        url: "",
        name: "",
        password: "",
        radio: 1,
        id: ""
      },
      tableData: [],
      loading: true
    };
  },
  components: {
    "v-nav": Nav
  },
  mounted() {
    this.getRepoList();
  },
  methods: {
    // 传递dialog的值
    handleChange(row) {
      console.log(row);
      this.dialogVisible = true;
      this.username = row.date;
      this.repo.id = row.id;
      this.repo.name = row.user;
      this.repo.password = row.password;
      this.repo.radio = row.type === "svn" ? 1 : 2;
      this.repo.url = row.repo_url;
    },
    // 判断svn https
    radioEvent(val) {
      this.repo.url = val === 2 ? "https://" : "svn://";
    },
    // 请求代码库列表
    getRepoList() {
      const url = "cap.api.views.repo.repo_all";
      getAndPost.get(url).then(res => {
        this.tableData = res.data.repo_list;
        this.loading = false;
        this.tableData.map(item => {
          item.type = item.type === 1 ? "svn" : "git";
          item.addtime = new Date(item.addtime * 1000).toLocaleString();
        });
      });
    },
    // 修改仓库信息
    changeRepo() {
      const url = "cap.api.views.repo.repo_edit";
      const obj = {
        id: this.repo.id,
        type: this.repo.radio,
        repo_url: this.repo.url,
        user: this.repo.name,
        password: this.repo.password
      };
      getAndPost.post(url, obj).then(res => {
        if (res.data === true) {
          this.getRepoList();
          this.dialogVisible = false;
          setTimeout(() => {
            this.$message({
              message: "修改成功",
              type: "success"
            });
          }, 100);
        } else {
          this.$message.error(res.formError.message);
        }
      });
    },
    // 删除代码库
    deleteChange(row) {
      console.log(row);
      const url = "cap.api.views.repo.repo_delete";
      const obj = {
        id: row.id
      };
      getAndPost.post(url, obj).then(res => {
        console.log(res);
        if (res.data === true) {
          this.getRepoList();
          this.dialogVisible = false;
          setTimeout(() => {
            this.$message({
              message: "删除成功",
              type: "success"
            });
          }, 100);
        } else {
          this.$message.error(res.formError.message);
        }
      });
    }
  }
};
</script>

<style scoped>
.el-table {
  color: black;
  font-size: 14px;
}

.state {
  background-color: #337ab7;
  color: #fff;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 7px;
  font-weight: 700;
  margin-right: 15px;
}

.users {
  font-size: 14px;
  font-weight: 700;
}

.el-checkbox-group,
.el-radio-group {
  width: 340px;
  display: inline-block;
}

.main {
  text-align: center;
}

.el-input {
  width: 320px;
  margin-left: 15px;
}
</style>



