<template>
<!-- 404页面 -->
  <div class="nocoming">
    <img src="../../../static/timg.jpeg" @click="goBack">
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  components: {},
  methods: {
    goBack() {
      this.$router.push("/cron");
    }
  }
};
</script>

<style scoped>
.nocoming {
  width: 100%;
}

.nocoming img {
  width: 100%;
  height: auto;
}
</style>



