<template>
  <div>
    <v-nav/>
    <div class="main">
      <div class="inpu">
        <span class="users">用户名&nbsp;&nbsp;&nbsp;</span>
        <el-input type="text" autosize placeholder="请输入用户名" v-model="userinfo.username">
        </el-input>
        <div style="margin-bottom:20px"></div>
        <span class="users">密&nbsp;&nbsp;&nbsp;码&nbsp;&nbsp;&nbsp;</span>
        <el-input type="password" autosize placeholder="请输入密码" v-model="userinfo.password">
        </el-input>
        <div style="margin-bottom:20px"></div>
        <span class="users">确认密码</span>
        <el-input type="password" autosize placeholder="请再次确认密码" v-model="userinfo.passwordYes" @blur="remind">
        </el-input>
        <div style="margin-bottom:20px"></div>
        <el-button type="primary" @click="addUser">
          <span class="login">确定</span>
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import getAndPost from "../common/utils";
import Nav from "./usernavigator";
export default {
  data() {
    return {
      userinfo: {
        username: "",
        password: "",
        passwordYes: ""
      }
    };
  },
  mounted() {
    this.getUserinfo();
  },
  methods: {
    // 检测两次密码一致性
    remind() {
      if (this.userinfo.password !== this.userinfo.passwordYes) {
        this.$message.error("两次密码输入不一致");
        return;
      }
    },
    // 添加用户 1验证密码 2添加
    addUser() {
      this.remind();
      const obj = {
        username: this.userinfo.username,
        password: this.userinfo.password,
        password_again: this.userinfo.passwordYes
      };
      const url = "cap.api.views.user.user_add";
      getAndPost.post(url, obj).then(res => {
        if (res.data === true) {
          this.$message({
            message: "添加成功",
            type: "success"
          });
          this.$router.push("userlist");
        } else {
          this.$message.error(res.formError.message);
        }
      });
    },
    getUserinfo() {
      const url = "cap.api.views.my.get_my_info";
      getAndPost.get(url).then(res => res.data);
    }
  },
  components: {
    "v-nav": Nav
  }
};
</script>

<style scoped>
.main {
  width: 100%;
  overflow: hidden;
  text-align: center;
}

.inpu {
  width: 500px;
  margin: 0 auto;
  border-radius: 20px;
  background-color: #fff;
}

.main .login {
  margin: 20;
}

.el-input {
  width: 320px;
  margin-left: 15px;
}

.users {
  font-size: 14px;
  font-weight: 700;
}

.title {
  line-height: 60px;
  font-size: 20px;
}

.el-checkbox-group,
.el-radio-group {
  width: 340px;
  display: inline-block;
}

.el-button {
  margin-bottom: 20px;
}
</style>



